import React from "react";
import "./styles.scss";

const agreeData = [
    {
        firstPoint:
            "This privacy policy (“Policy”) explains how personally identifiable information is collected, used and disclosed by Motto (“Motto”, “we” or “us”) with respect to your access and use of Motto’s mobile web application and the websites located at bookmotto.com, (collectively, the “Application”) and the booking and other services available through the Application (collectively, the “Service”) so you can make an informed decision about using the Service.",
        secondPoint:
            "We reserve the right to change the provisions of this Policy at any time. We will alert you that changes have been made by indicating on this Policy the date it was last updated. We encourage you to review this Policy from time-to-time to make sure that you understand how any personally identifiable information you provide to us or collected about you will be used.",
        headingOne: "WHAT IS PERSONAL INFORMATION?",
        thridPoint:
            "As used in this Policy, the term 'personally identifiable information' means information that specifically identifies an individual (such as a name, user name or e-mail address), or information about that individual that is directly linked to information that specifically identifies an individual. Personally identifiable information does not include 'aggregate' information, which is data we collect about the use of the Service or about a group or category of services or users, from which individual identities or other personally identifiable information has been removed. This Policy in no way restricts or limits our collection and use of aggregate information.",
        headingTwo: "WHAT INFORMATION DO WE COLLECT?",
        spanOne: "Active Collection: ",
        fouthPoint:
            "We collect information from you in various ways when you use the Service. For example, when you use the Service, we collect information: (i) that is provided to us (such as your name, email address, telephone number, date of birth, credit card number, expiration date, authentication code, or similar information); (ii) to which you provide us access; (iii) about you that is publicly available; and (iv) received by us about you from third-party sources (such as fitness clubs or credit agencies). You can choose not to provide certain personally identifiable information but, if you choose to do so, you might not be able to take advantage of the Service.",
        spanTwo: "Passive Collection:",
        fivthPoint:
            " When you use the Service, some information is also automatically collected, such as your Internet Protocol (IP) address, your operating system, the browser type, the address of a referring website, and your activity on the Service. We may also automatically collect certain logistical information in server logs, including information about how you use various features of the Service and information about the number, frequency and length of each session.",
        sixthPoint:
            "We may also automatically collect certain information through the use of 'cookies'.Cookies are small data files stored on your hard drive at the request of a website. Among other things, cookies help us to improve the Service and your experience. If we link cookies to any personally identifiable information, we will treat this information as personally identifiable information. If you wish to block, erase, or be warned of cookies, please refer to your browser manufacturer to learn about these functions. However, if you choose to remove or reject cookies, this could affect certain features or services made available via the Service.",
        seventhPoint:
            "We may also employ the use of anonymous identifiers, which are random strings of characters that are used for the same purposes as cookies on platforms, including certain mobile devices, where cookie technology is not available.",
        eightPoint:
            "We may use Web beacons or similar technology on the Service. Web beacons are small, invisible graphic images that may be used on the Service or in emails relating to the Service to collect certain information and monitor user activity on the Service, such as to count visits, understand usage effectiveness and to tell if an email has been opened and acted upon.",
        ninthPoint:
            "We may collect device-specific information (such as your hardware model, operating system version, unique device identifiers, and mobile network information including phone number). We may associate your device identifiers or phone number with your Motto account.",
        tenPoint:
            "Personally identifiable information will be stored on secured servers, and we may store and process personally identifiable information in the United States and other countries.",
        headingThree:
            "HOW DO WE USE THE PERSONALLY IDENTIFIABLE INFORMATION WE COLLECT?",
        elevanthPoint:
            "We use personally identifiable information collected through the Service for the purposes described in this Policy or elsewhere on the Service. For example, we may use personally identifiable information we collect:",
    },
];
const PrivacyPolicy = () => {
    return (
        <div className="outer_privacy">
            <div className="container">
                <div className="inner_privacy">
                    <div className="privacy_heading">
                        <h1>Privacy Policy</h1>
                    </div>
                    <div className="privacy_wrapper">
                        {agreeData.map((data, index) => (
                            <div className="privacy_inner_data" key={index}>
                                <p>{data.firstPoint}</p>
                                <p>{data.secondPoint}</p>
                                <h2>{data.headingOne}</h2>
                                <p>{data.thridPoint}</p>
                                <h2>{data.headingTwo}</h2>
                                <p>
                                    <span style={{ fontWeight: 800 }}>
                                        {data.spanOne}
                                    </span>
                                    {data.fouthPoint}
                                </p>
                                <p>
                                    <span style={{ fontWeight: 800 }}>
                                        {data.spanTwo}
                                    </span>
                                    {data.fivthPoint}
                                </p>
                                <p>{data.sixthPoint}</p>
                                <p>{data.seventhPoint}</p>
                                <p>{data.eightPoint}</p>
                                <p>{data.ninthPoint}</p>
                                <p>{data.tenPoint}</p>
                            </div>
                        ))}
                    </div>
                    <SectionTwo
                        heading="HOW DO WE USE THE PERSONALLY IDENTIFIABLE INFORMATION WE COLLECT?"
                        desc="We use personally identifiable information collected through the Service for the purposes described in this Policy or elsewhere on the Service. For example, we may use personally identifiable information we collect:"
                        list="to process and complete any transactions in connection the Service (e.g., to sell you a personal training session);"
                    />
                    <SectionList list="to request feedback and to otherwise contact you about your use of the Service" />
                    <SectionList list="to respond to your emails, questions, comments, requests and complaints, and to provide customer service;" />
                    <SectionList list="to monitor and analyze Service usage and trends;" />
                    <SectionList list="to personalize and improve the Service, and our users' experiences with the Service (such as providing content or features that match interests), and to increase the Service's functionality and user friendliness;" />
                    <SectionList list="to provide you with news and information about our or third-party events, offers, promotions and services we think will be of interest to you;" />
                    <SectionList list="to send you confirmations, updates, security alerts and support and administrative messages, and otherwise facilitate your use of, and our administration and operation of, the Service;" />
                    <SectionList list="to notify you about important changes to the Service; and" />
                    <SectionList list="for any other purpose for which the information was collected." />
                    <SectionTwo
                        heading="WHAT PERSONALLY IDENTIFIABLE INFORMATION DO WE SHARE WITH THIRD-PARTIES?"
                        desc="We will not knowingly share the personally identifiable information we collect from you through the Service with third-parties, except as described in this Policy or on the Service. For example, we may share personally identifiable information as follows:"
                        list="with vendors, consultants, and other service providers ('Service Providers') who are engaged by or working with us in connection with the operation of the Service and who need access to such information to carry out their work for us;"
                    />
                    <SectionList list="with fitness clubs and personal trainers where you have purchased a training session through the Service;" />
                    <SectionList list="when you give us your consent to do so, including if we notify you on the Service that the information you provide will be shared in a particular manner and you provide such information;" />
                    <SectionList list="in an aggregated or anonymized form that does not directly identify you;" />
                    <SectionList list="when we believe in good faith that we are lawfully authorized or required to do so or that doing so is reasonably necessary or appropriate to comply with the law or legal processes or to respond to lawful requests or legal authorities including, but not limited to, responding to lawful subpoenas, warrants, or court orders;" />
                    <SectionList list="to enforce or apply this Policy, the Terms, or our other policies or agreements; and" />
                    <SectionList list="in connection with, or during negotiations of, any merger, sale of company assets, financing or acquisition, or in any other situation where personally identifiable information may be disclosed or transferred as one of the business assets of Motto." />
                    <SectionHeading desc="You understand that when you use the Service, information you provide via the Service, information you grant access to by using the Service, as well as any information you share with individuals through the Service, will be available to other users and in some cases may be publicly available (with the exception of personally identifiable information, which shall not become publicly available, unless such personally identifiable information is shared by you as part of a review, photo submission or other similar method)" />
                    <SectionHeading
                        desc="We are not responsible for the actions of Service 
                        Providers or other third-parties, nor are we responsible for 
                        any additional information you provide directly to any other users or third-parties, 
                        and we encourage you to become familiar with third-party privacy practices before disclosing 
                        ormation directly to any such third-parties. Nothing herein restricts the sharing of
                         aggregated or anonymized information, which may be shared with third-parties without your 
                         consent."
                    />
                    <SectionHeading
                        heading="LINKS TO OTHER WEBSITES, SOCIAL MEDIA (FEATURES) AND WIDGETS"
                        desc="The Service may contain links to other websites. Any personally identifiable information you provide on the linked pages is provided directly to that third- party and is subject to that third-party's privacy policy. This Policy does not apply to such linked websites, and we are not responsible for the content or privacy and security practices and policies of these websites or any other websites that are linked to/from the Service. We encourage you to learn about their privacy and security practices and policies before providing them with personally identifiable information."
                    />
                    <SectionHeading
                        heading="WHAT STEPS DO WE TAKE TO PROTECT YOUR INFORMATION ONLINE?"
                        desc="We take reasonable measures to protect your personally identifiable information in an effort to prevent loss, misuse, and unauthorized access, disclosure, alteration, and destruction. Please be aware, however, that despite our efforts, no security measures are perfect or impenetrable, and no method of data transmission can guarantee against any interception or any other type of misuse. To assist with the protection of personally identifiable information, you must keep your password confidential and not disclose it to any other person. You are responsible for all uses of the Service by any person using your password. Please advise us immediately if you believe your password has been misused."
                    />
                    <SectionHeading
                        heading="WHAT CHOICES DO YOU HAVE REGARDING THE USE OF YOUR INFORMATION?"
                        desc="You may 'opt out' of receiving marketing or promotional emails from us by following the instructions in those emails. You may also opt out of such marketing emails by following the instructions within such emails or emailing us at 'contact@bookmotto.com'. If you opt out, we may still send you non-promotional emails, such as emails about your account, session information or our ongoing business relations."
                    />
                    <SectionLink
                        heading="QUESTIONS?"
                        desc="If you have any questions about this Policy, please contact us at "
                        hyper="contact@bookmotto.com ."
                    />
                </div>
            </div>
        </div>
    );
};
const SectionList = (props) => {
    return (
        <div className="div_outer_wrapper box2">
            <ul>
                <li className="common_margin_li">
                    <span>{props.list}</span>
                    <a>{props.hyper}</a>
                </li>
            </ul>
        </div>
    );
};
const SectionTwo = (props) => {
    return (
        <div className="div_outer_wrapper box2">
            <h2>{props.heading}</h2>
            <p className="common_margin">{props.desc}</p>
            <ul>
                <li className="common_margin_li">
                    <span>
                        {props.list} <a>{props.hyper}</a>
                    </span>
                </li>
            </ul>
        </div>
    );
};
const SectionLink = (props) => {
    return (
        <div className="div_outer_wrapper box2">
            <h2>{props.heading}</h2>
            <p className="common_margin">
                {props.desc}
                <a href="mailto:contact@bookmotto.com" target="_blank">
                    {props.hyper}
                </a>
            </p>
        </div>
    );
};
const SectionHeading = (props) => {
    return (
        <div className="div_outer_wrapper box2">
            <h2>{props.heading}</h2>
            <p className="common_margin">{props.desc}</p>
        </div>
    );
};

export default PrivacyPolicy;
